.fade-in-container {
  height: 100%;
  opacity: 0;
  transform: translateY(100px);
  visibility: hidden;
  transition: opacity 500ms ease-out, transform 500ms ease-out;
  will-change: opacity, visibility;
}

.fade-in-container.is-visible {
  opacity: 1;
  transform: none;
  visibility: visible;
}
