.content-block-actions-container {
  display: none;
  position: absolute;
  top: 8px;
  right: 8px;
  z-index: 3;
}

.content-container.edit
  .content-block-container.highlighted-block
  .content-block-actions-container,
.content-container.edit
  .content-block-container:hover
  .content-block-actions-container {
  display: block;
}
