.content-block-container {
  position: relative;
  cursor: default;
  /* overflow: hidden; */
  /* border-radius: 4px; */
}

.content-container.edit .content-block-container.highlighted-block,
.content-container.edit .content-block-container:hover {
  outline: 1px solid #b0bec5;
}

.content-container.edit .content-block-container.not-highlighted-block {
  opacity: 0.5;
}

.content-block {
  position: relative;
  display: flex;
  max-width: 100%;
}
