.content-container .term {
  white-space: nowrap;
}

.content-container.example p span {
  color: transparent;
}
.content-container.example p .word,
.content-container.example p .term {
  background: #cfd8dc;
  border-radius: 4px;
}

.content-container.monitoring-record .recognized {
  background-color: #c5e1a5;
}
.content-container.monitoring-record .unrecognized {
  background-color: #ffcdd2;
}
.content-container.monitoring-record .unread {
  background-color: #cfd8dc;
}
